.mobile-menu {
  width: 60%;
  height: 100%;
  background-color: var(--mobile-menu-bg-theme);
  z-index: 100;
  justify-content: center;
  align-items: center;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  transform: translate(100%);
}

.mobile-menu.is-open {
  transform: translate(0);
}

.backdrop {
  width: 40%;
  height: 100%;
  z-index: 100;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  background-color: rgba(128, 128, 128, .43);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.label-check {
  position: absolute;
  bottom: 20px;
  left: 20px;
}

.mobile-icon {
  width: 83px;
  height: 18px;
}

.switch__input {
  -ms-appearance: none;
  appearance: none;
}

.switch-desk, .switch-mob {
  width: 46px;
  height: 20px;
  border-radius: 14px;
  display: inline-block;
  position: relative;
  box-shadow: 7px 5px 15px rgba(22, 22, 22, .6);
}

.switch__slider {
  cursor: pointer;
  background-color: #cecdcd;
  border-radius: 18px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.switch__slider:before {
  content: "";
  cursor: pointer;
  height: 16px;
  width: 16px;
  background-color: var(--primary-bg-theme);
  border-radius: 50%;
  transition: all .25s;
  position: absolute;
  bottom: 2px;
  left: 2px;
}

.switch__input:checked + .switch__slider {
  background: linear-gradient(91deg, #9bb537 0%, rgba(155, 181, 55, .69) 100%);
  box-shadow: 7px 5px 15px rgba(22, 22, 22, .6);
}

.switch__input:checked + .switch__slider:before {
  transform: translateX(26px);
}

